* {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#Map {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: rgba(0, 0, 0, 0.1);
}

.Map-Loader {
  display: block;
  position: relative;
  width: 64px;
  height: 64px;
  top: 50%;
  margin: auto;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.Map-Loader div {
  position: absolute;
  top: 27px;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background: #00194B;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.Map-Loader div:nth-child(1) {
  left: 6px;
  -webkit-animation: lds-ellipsis1 0.6s infinite;
          animation: lds-ellipsis1 0.6s infinite;
}
.Map-Loader div:nth-child(2) {
  left: 6px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.Map-Loader div:nth-child(3) {
  left: 26px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.Map-Loader div:nth-child(4) {
  left: 45px;
  -webkit-animation: lds-ellipsis3 0.6s infinite;
          animation: lds-ellipsis3 0.6s infinite;
}
@-webkit-keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@-webkit-keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0);
  }
}

.SearchResults {
    background-color: white;
    border-radius: 4px;
    overflow-y: scroll;
    max-height: calc(100vh - 68px);
}

.SearchResult {
    padding: 6px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow-x: hidden;
    cursor: default;
    font-size: 14px;
}

.SearchResult:hover {
    background-color: rgba(0, 0, 0, 0.15);
}

.SearchResult-Image {
    height: 20px;
    margin: 0 12px 0 6px;
    vertical-align: middle;
}

.SearchResult-Name {
    vertical-align: middle;
}

.SearchResult-Id {
    color: rgba(0, 0, 0, 0.7);
    vertical-align: middle;
    margin-left: 8px;
}
.SearchBar {
    width: 100%;
    background-color: white;
    box-shadow: 0px 2px 7px 1px rgba(0, 0, 0, 0.24);
    border-radius: 4px;
}

.SearchBar-Inputs {
    display: flex;
    flex-direction: row;
    height: 44px;
}

.SearchBar-Input {
    flex-grow: 1;
    background: transparent;
    font-size: 16px;
    padding: 12px 16px;
    outline: none;
    border: none;
}

.SearchBar-ModeButton::before {
    content: "";
    position: absolute;
    margin: 6px auto 6px -4px;
    border-left: 1px solid #ddd;
    height: 24px;
}

.SearchBar-ModeButton {
    text-align: right;
    padding: 4px;
}

.SearchBar-ModeButton-Icon {
    height: 36px;
    width: 36px;
    padding: 4px;
    cursor: pointer;
    border-radius: 50%;
}

.SearchBar-ModeButton-Icon:hover {
    background-color: rgba(0, 0, 0, 0.14);
}
.DirectionsResults {
    background-color: white;
    border-radius: 4px;
    overflow-y: scroll;
    max-height: calc(100vh - 148px);
}

.DirectionsResults-None {
    padding: 8px;
    text-align: center;
}
.DirectionsResult {
    width: 100%;
    height: 56px;
    display: grid;
    background-color: white;
    grid-template-columns: 40px auto;
    grid-column-gap: 8px;
    grid-template-areas:
        'symbol title'
        'symbol info';
    align-items: center;
    border-radius: 4px;
    padding: 6px;
}

.DirectionsResult-Symbol {
    height: 12px;
    width: 12px;
    line-height: 40px;
    grid-area: symbol;
    border: 2px solid black;
    border-radius: 50%;
    margin: auto;
    background-color: white;
    z-index: 1;
}

.DirectionsResult-Line-Top {
    width: 8px;
    height: 20px;
    background-color: black;
    grid-area: symbol;
    margin: 0 auto;
    align-self: start;
}

.DirectionsResult-Line-Bottom {
    width: 8px;
    height: 20px;
    background-color: black;
    grid-area: symbol;
    margin: 0 auto;
    align-self: end;
}

.DirectionsResult-Line-Full {
    width: 8px;
    height: 72px;
    background-color: black;
    grid-area: symbol;
    margin: -16px auto;
}

.DirectionsResult-Title {
    font-size: 18px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow-x: hidden;
    grid-area: title;
}

.DirectionsResult-Info {
    font-size: 14px;
    color: rgba(0, 0, 0, 0.54);
    grid-area: info;
}

.DirectionsResult-Route {
    display: inline-block;
    color: white;
    border-radius: 4px;
    padding: 2px 6px;
    margin-right: 4px;
}

.DirectionsResult-Walk {
    vertical-align: bottom;
}
.DirectionsBar {
    width: 100%;
    background-color: #00194B;
    box-shadow: 0px 2px 7px 1px rgba(0, 0, 0, 0.24);
    border-radius: 4px;
}

.DirectionsBar-Header {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.DirectionsBar-Header-Input {
    width: 100%;
    background: transparent;
    color: white;
    font-size: 16px;
    padding: 12px 16px;
    outline: none;
    border: none;
    margin: 0;
}

.DirectionsBar-Header-Input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.65);
}

.DirectionsBar-Header-Input::-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.65);
}

.DirectionsBar-Header-Input::placeholder {
    color: rgba(255, 255, 255, 0.65);
}

.DirectionsBar-Header-Close {
    padding: 8px;
}

.DirectionsBar-Header-Close-Button {
    cursor: pointer;
    width: 24px;
    height: 24px;
}

.DirectionsBar-RouteSet {
    width: 100%;
    padding: 7px 16px;
    border: none;
    outline: none;
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
    font-size: 12px;
    border-radius: 4px;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    text-transform: uppercase;
    font-weight: 600;
    cursor: pointer;
    line-height: 24px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.DirectionsBar-RouteSet > svg {
    float: right;
}
.SideBar {
    position: fixed;
    top: 0;
    left: 0;
    padding: 12px;
    width: 400px;
}
.Prediction {
    width: 100%;
    height: 40px;
    display: grid;
    background-color: white;
    grid-template-columns: 40px auto;
    grid-column-gap: 8px;
    grid-template-areas:
        'route-id route-name'
        'route-id time';
    align-items: center;
    margin-top: 4px;
}

.Prediction-RouteId {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: gray;
    color: white;
    text-align: center;
    line-height: 40px;
    grid-area: route-id;
}

.Prediction-RouteName {
    font-size: 12px;
    color: rgba(0, 0, 0, 0.65);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow-x: hidden;
    grid-area: route-name;
}

.Prediction-Time {
    font-size: 20px;
    grid-area: time;
}
.Predictions-Wrapper {
    position: fixed;
    left: 0px;
    bottom: 0px;
    width: 400px;
    padding: 12px;
}

.Predictions {
    padding: 8px;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0px 2px 7px 1px rgba(0, 0, 0, 0.24);
}

.Predictions-Header {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.Predictions-Stop {
    flex-grow: 1;
    overflow: hidden;
}

.Predictions-StopName {
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow-x: hidden;
}

.Predictions-StopId {
    color: rgba(0, 0, 0, 0.54);
    font-size: 14px;
    margin: 0 0 4px 0;
}

.Predictions-Close {
    cursor: pointer;
}

.Predictions-List {
    display: flex;
    flex-direction: row;
}

.Predictions-List > div {
    width: 50%;
}

.Predictions-Note {
    text-align: center;
    width: 100%;
}
